import { useMediaQuery } from 'react-responsive';

const ResponsiveStyle = () => {
  //          
  const isSmallScreen = useMediaQuery({ maxWidth: 768 }); //   768px  
  const isMediumScreen = useMediaQuery({ minWidth: 769, maxWidth: 1024 }); //   769px  1024px  
  const isLargeScreen = useMediaQuery({ minWidth: 1025 }); //   1025px  

  let spotWidth, spotHeight, fontSize, titleFontSize, padding, margin, motionPos;
  
  if (isSmallScreen) {
    spotWidth = 80; 
    spotHeight = 60; 
    fontSize = 'x-small';
    titleFontSize = '25px';
    padding = '5px';
    margin = '20px';
    motionPos = 20;
  } else if (isMediumScreen) {
    spotWidth = 100; 
    spotHeight = 80; 
    fontSize = 'small';
    titleFontSize = '35px';
    padding = '7px';
    margin = '40px';
    motionPos = 25;
  } else if (isLargeScreen) {
    spotWidth = 120; 
    spotHeight = 100; 
    fontSize = 'medium';
    titleFontSize = '50px';
    padding = '9px';
    margin = '80px';
    motionPos = 30;
  }

  return {
    spotWidth,
    spotHeight,
    fontSize,
    titleFontSize,
    padding,
    margin,
    motionPos,
  };
};

export default ResponsiveStyle;
